/* === Base Navbar Styles === */
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #0d0d0d;
  color: #fff;
  padding: 1rem 3rem;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.logo {
  font-size: 1.6rem;
  font-weight: 700;
  letter-spacing: 1px;
}

.nav-links {
  list-style: none;
  display: flex;
  gap: 1.8rem;
}

.nav-links a {
  color: #fff;
  text-decoration: none;
  font-weight: 500;
  transition: color 0.3s ease;
}

.nav-links a:hover {
  color: #00bcd4;
}

/* === Hamburger Menu (Hidden on Desktop) === */
.menu-toggle {
  display: none;
  font-size: 2rem;
  cursor: pointer;
  color: #fff;
}

/* === Responsive Styles === */
@media (max-width: 768px) {
  /* Show hamburger icon */
  .menu-toggle {
    display: block;
  }

  /* Stack links vertically and hide them by default */
  .nav-links {
    position: absolute;
    top: 70px; /* adjust to match navbar height */
    right: 0;
    width: 100%;
    background-color: #0d0d0d;
    flex-direction: column;
    align-items: flex-start;
    padding: 1rem 2rem;
    gap: 1rem;
    display: none; /* Hidden until toggled */
  }

  /* When menu is active (JS adds .active) */
  .nav-links.active {
    display: flex;
  }
}
